@import "../../assets/css/base/styles";
.login-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 3;
  background: url("#{$images-root}/1.jpg");
  height: 100%;
  min-height: 700px;
  background-size: cover;
  background-repeat: no-repeat;
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: $black;
    opacity: 0.4;
    z-index: 3;
  }
  .login-content {
    z-index: 5;
    position: relative;
    height: 100%;
    justify-content: space-around;
    align-items: center;
    margin: 0;
    @media (max-width: 992px) {
      margin: 0 15px;
    }
    .col {
      position: relative;
      background-color: rgba($color: $white, $alpha: 0.5);
      border-radius: 7px;
      padding: 20px;
      &::before {
        content: "";
        position: absolute;
        top: 5px;
        left: 5px;
        width: 100px;
        height: 100px;
        border: 2px dashed $color-purple;
        border-bottom: 0;
        border-right: 0;
        z-index: 0;
      }
      &::after {
        content: "";
        position: absolute;
        bottom: 5px;
        right: 5px;
        width: 100px;
        height: 100px;
        border: 2px dashed $color-purple;
        border-top: 0;
        border-left: 0;
        z-index: 0;
      }

      form {
        position: relative;
        z-index: 2;

        input {
          &:focus {
            border-color: $color-purple;
            @include prefix(box-shadow, 0 0 0 0.25rem $color-shadow);
          }
          &.invalid {
            border-color: $color-invalid;
          }
        }
      }

      label {
        text-align: right;
        width: 100%;
        padding-right: 5px;
      }
      .visiblePassword {
        position: absolute;
        top: 12px;
        left: 9px;
        color: $color-info;
        z-index: 4;
      }
     
      .invalid-text {
        font-size: 13px;
        color: $color-invalid;
        text-align: right !important;
        padding-right: 5px;
      }
      .invalidInput {
        text-align: right !important;
        padding-right: 5px;
      }
      button {
        width: 100%;
        color: $white;
        background-color: $color-purple;
      }
    }
  }
};